import React , { Component } from 'react';
import { BrowserRouter , Route , Redirect} from 'react-router-dom';
import { connect } from 'react-redux';
import * as actions from '../actions';

import Header from './Header';
import Landing from './Landing';
const Dashboard = () => {return <h1>Dashboard</h1>}
const SurveyNew = () => {return <h1>SurveyNew</h1>}

class App extends Component {

componentDidMount(){
  this.props.fetchUser();
}

  render(){
    return (
        <BrowserRouter>
            <Header />
            <Route path="/" exact component={Landing} /> 
            {this.props.auth ? <Route path="/surveys" exact component={Dashboard} /> : <Redirect from="/surveys" to="/" /> }
            {this.props.auth ? <Route path="/surveys/new" component={SurveyNew} /> : <Redirect from="/surveys/new" to="/" />}  
        </BrowserRouter>  
    )
  }  
}

const mapStateToProps = ({auth}) => {
  return {
    auth
  }
}

export default connect(mapStateToProps, actions)(App);